.bottom-bar {
    transition: all .2s ease-in-out
}

.bottom-bar.show {
    transform: translateY(0)
}

.bottom-bar.hide {
    transform: translateY(100%)
}

.focus-actions {
    display: none;
    position: absolute
}

.focus-actions.show {
    display: inline-block
}

.focus-actions.show+.focus-hide {
    display: none
}

.focus-actions-trigger {
    position: relative
}

.focus-actions-trigger:focus-within .focus-actions {
    z-index: 1;
    display: flex
}

.focus-actions-trigger:focus-within .focus-hide {
    display: none
}

.focus-bg-primary:active,
.focus-bg-primary:focus,
.focus-bg-primary:focus-visible {
    background-color: var(--falcon-primary) !important;
    color: var(--falcon-white) !important
}

.focus-bg-secondary:active,
.focus-bg-secondary:focus,
.focus-bg-secondary:focus-visible {
    background-color: var(--falcon-secondary) !important;
    color: var(--falcon-white) !important
}

.collapse-indicator-plus {
    position: relative
}

.collapse-indicator-plus:after {
    content: "+";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    font-size: 1.2rem;
    line-height: 0;
    font-weight: 500;
    color: inherit;
    transition: all .2s ease-in-out;
    transition-property: transform
}

.collapse-indicator-plus:before {
    display: none
}

.collapse-indicator-plus[aria-expanded=true]:after {
    margin-left: 2px;
    margin-bottom: 6px;
    content: "−";
    transform: rotate(-180deg)
}

